import React, { useContext, useState, useEffect } from "react";

import "./ClientList.css";

// import { PopContext } from "./PopContext";
import { OrderContext } from "./contexts/OrderProvider";

export default function ClientList() {
	const { state, setState } = useContext(OrderContext);

	// state = {
	// 	selected: 0
	// };

	// componentDidMount() {
	// 	let value = this.context.state.clients;
	// 	// console.log("value :", value);
	// 	/* perform a side-effect at mount using the value of UserContext */
	// }

	const handleUserClick = client => event => {
		setState({ ...state, currentClient: client });
	};

	const { clients } = state;

	return (
		<div className="client-list">
			{clients.map(client => {
				return (
					<div
						key={client.id}
						className="client-name"
						onClick={handleUserClick(client)}
					>
						{client.name}
					</div>
				);
			})}
		</div>
	);
}

