import React, { Component } from "react";

import "./UserBelt.css";

import { PopContext } from "./PopContext";

class UserBelt extends Component {
	state = {
		selected: 0
	};
	componentDidMount() {
		let value = this.context.state.clients;
		// console.log("value :", value);
		/* perform a side-effect at mount using the value of UserContext */
	}
	render() {
		return (
			<React.Fragment>
				<PopContext.Consumer>
					{context => (
						<div className="client-list">
							{context.state.clients.map(client => {
								return (
									<div key={client.id} className="client-name">
										{client.name}
									</div>
								);
							})}
						</div>
					)}
				</PopContext.Consumer>
			</React.Fragment>
		);
	}
}

UserBelt.contextType = PopContext;

export default UserBelt;
