const products = [
	{
		id: uniqueId(),
		name: "tequeño"
	},
	{
		id: uniqueId(),
		name: "pastelito"
	},
	{
		id: uniqueId(),
		name: "mandoca"
	}
];

export class PopProvider extends Component {
	state = {
		products: products,
		client: null,
		clients: []
	};

	addUser = user => {
		const newClients = Array.from(this.state.clients);
		const client = {
			name: user,
			id: uniqueId(),
			order: []
		};
		newClients.push(client);
		newClients.sort((a, b) => (a.id > b.id ? 1 : -1));

		this.setState({ clients: newClients.reverse(), client }, () => {
			// console.log(this.state.clients);
		});
	};

	addItem = item => {
		const newItem = {};
	};

	setClient = client => {
		const nclient = this.state.clients.filter(
			lclient => lclient.id === client.id
		);
		const [stateclient] = nclient;
		console.log("stateclient :", stateclient);
		// console.log(mclient);
		this.setState({
			client: stateclient
		});
	};

	setItemQuantity = id => {
		// loop on the current user order
		// const packedItems = items.filter(item => item.packed);

		// checks if there's an item associated
		const item = this.state.client.order.filter(item => item.itemId === id);

		// this.setItemQuantity(id);

		if (item.length > 0) {
			return item[0].quantity;
		}
		return 0;

		// match the id with the product id
		// return the quantity
		// if no id match, return 0
	};

	onChangeItem = (event, itemId) => {
		// {
		// 	name: "Javier",
		// 	id: uniqueId(),
		// 	order: [
		// 		{
		// 			itemName: "tequeno",
		// 			itemId: 0,
		// 			quantity: 3
		// 		},
		// 		{
		// 			itemName: "pastelpapa", // no se si el nombre aqui vaya
		// 			itemId: 1,
		// 			quantity: 5
		// 		}
		// 	]
		// },
		const quantity = parseInt(event.target.value);
		const newClients = this.state.clients.map(client => {
			if (client.id === this.state.client.id) {
				console.log("cambiando", client.name);
				// debugger;
				if (client.order.length > 0) {
					console.log("no items :");
				} else {
					console.log("pushieando :", quantity);
					client.order.push({
						itemId,
						quantity
					});
				}
			}
			return client;
		});
		console.log(newClients);

		this.setState({ clients: newClients });
	};

	render() {
		return (
			<PopContext.Provider
				value={{
					state: this.state,
					// increaseOrder: () => this.setState({ amount: this.state.amount + 1 }),
					//increaseItem: (type) => this.setState({ type: this.state.type + 1 }), // interesting...
					addUser: this.addUser,
					// setUser: this.setUser,
					setClient: this.setClient,
					setItemQuantity: this.setItemQuantity,
					onChangeItem: this.onChangeItem
				}}
			>
				{this.props.children}
			</PopContext.Provider>
		);
	}
}

