import React, { useContext, useState, useEffect } from "react";
import { PopContext } from "./PopContext";
// import isEmpty from "lodash/isEmpty";
import "./ProductList.css";

import { OrderContext } from "./contexts/OrderProvider";

export default function ProductList() {
	const { state, setState } = useContext(OrderContext);
	const { clients, currentClient, products } = state;

	// constructor(props) {
	// 	super(props);

	// 	this.state = {
	// 		// client: this.context.state.client
	// 	};

	// 	this.handleQuantityChange = this.handleQuantityChange.bind(this);
	// }
	console.log("state is ", state);

	// handleQuantityChange(event) {
	// 	console.log("the on change is working");
	// 	const tequenos = parseInt(event.target.value);
	// 	this.setState({ tequenos });
	// }

	// productQuantitator = (quantity, product) => {
	// 	for (let index = 0; index <= quantity; index++) {}
	// };

	// componentDidMount() {
	// 	// this.setOrder();
	// 	// console.log(this.context.state.clients);
	// 	/* perform a side-effect at mount using the value of UserContext */
	// }

	const setItemQuantity = id => {
		// loop on the current user order
		// const packedItems = items.filter(item => item.packed);

		// checks if there's an item associated

		const item = currentClient.order.filter(item => item.itemId === id);

		// this.setItemQuantity(id);
		if (item.length > 0) {
			return item[0].quantity;
		}
		return 0;

		// match the id with the product id
		// return the quantity
		// if no id match, return 0
	};

	const onChangeQuantity = (e, productId) => {
		// debugger;
		const quantity = parseInt(e.target.value);
		const currentProductQuantity = state[productId];
		let newProductQuantity = null;
		// debugger;

		// first, i need to find the client
		const newClients = clients.map(client => {
			// current client is client to be changed?
			if (currentClient.id === client.id) {
				// check if its a new product
				let isNewProduct = true;
				client.order.forEach(item => {
					if (item.itemId === productId) isNewProduct = false;
				});

				let newOrder = [];

				if (isNewProduct) {
					newOrder = [
						...client.order,
						{
							itemId: productId,
							quantity
						}
					];
					newProductQuantity = quantity;
				} else {
					newOrder = client.order.map(item => {
						// if so, updated
						if (item.itemId === productId) {
							if (currentProductQuantity > quantity) {
								newProductQuantity = currentProductQuantity - quantity;
							} else {
								newProductQuantity = currentProductQuantity + quantity;
							}
							return { ...item, quantity };
						}
						return item;
					});
				}
				client.order = newOrder;
			}
			return client;
		});
		setState({
			...state,
			clients: newClients,
			[productId]: newProductQuantity
		});
	};

	const buildOrderMenuFromProducts = productList => {
		const list = productList.map(product => {
			return (
				<ItemSelector
					key={product.id}
					name={product.name}
					id={product.id}
					clientQuantity={setItemQuantity(product.id)}
					// onChange={e => this.context.onChangeItem(e, product.id)}
					onChange={e => onChangeQuantity(e, product.id)}
				/>
			);
		});
		return list;
	};

	return (
		<div className="product-list">
			{currentClient && buildOrderMenuFromProducts(products)}
		</div>
	);
}

function ItemSelector(props) {
	const { name, id, clientQuantity, onChange } = props;
	return (
		<div key={id} className="product">
			<span className="product-name">{name}</span>
			<select
				value={clientQuantity}
				onChange={onChange}
				className="product-select"
			>
				<option value="0">0</option>
				<option value="1">1</option>
				<option value="2">2</option>
				<option value="3">3</option>
				<option value="4">4</option>
				<option value="5">5</option>
			</select>
		</div>
	);
}

