import React, { useContext, useState, useEffect } from "react";
import logo from "../img/logo.jpg";
import warning from "../img/icomoon/SVG/alert-triangle.svg";
import add from "../img/icomoon/SVG/plus-circle.svg";
import uniqueId from "lodash/uniqueId";

// import uniqueId from "lodash/uniqueId";

import { OrderContext } from "./contexts/OrderProvider";

import "./Header.css";

function Header() {
	const { state, setState } = useContext(OrderContext);
	const [currentClient, setCurrentClient] = useState("");
	// console.log("state is ", state);

	// state = {
	// 	name: "",
	// 	enableAdding: false
	// };

	// addUser = e => {
	// 	//console.log(this.state.name);
	// 	// this.props.context.addUser(this.state.name);
	// 	// console.log(this.context.addUser(this.state.name));
	// };

	const addUser = user => {
		const newClients = Array.from(state.clients);
		const client = {
			name: user,
			id: state.clients.length + 1,
			order: []
		};
		newClients.push(client);
		newClients.sort((a, b) => (a.id > b.id ? 1 : -1));

		setState({
			...state,
			clients: newClients.reverse(),
			currentClient: client
		});
	};

	const handleValueChange = event => {
		// const value = event.target.value.replace(/^0+/, "");
		// const newvalue = Math.abs(value);
		setCurrentClient(event.target.value);
	};

	const handleClick = event => {
		if (currentClient.length > 1) {
			const clientName = currentClient;
			addUser(clientName);
		}
	};

	return (
		<div className="header">
			<div className="logo">
				<div className="logo-written">
					<p>TEQUEPOPS-CALC!</p>
				</div>
				<div className="logo-image">
					<img className="logo-jpg" src={logo} alt="" />
				</div>
			</div>
			<div className="username-input">
				<div className="username">
					<input
						value={currentClient}
						type="text"
						placeholder="QUIEN COME?"
						onChange={handleValueChange}
					/>
				</div>
				<div className="buttons">
					<div onClick={handleClick} className="btn-add-user">
						<img className="icon" src={add} alt="" />
						<span className="icon-label">AGREGA</span>
					</div>
					<div className="btn-reset-app">
						<img className="icon" src={warning} alt="" />
						<span className="icon-label">RESET</span>
					</div>
				</div>
			</div>
		</div>
	);
}

export default Header;
